<template>
  <i
      :class="className"
      class="vux-key vux_hairline"
      @mousemove="onBlur"
      @mouseup="onBlur"
      @touchcancel="onBlur"
      @touchend="onBlur"
      @touchmove="onBlur"
      v-text="text"
      @touchstart.stop.prevent="onFocus"
      @mousedown.stop.prevent="onFocus"
  />
</template>

<script>
export default {
  name: 'vux-key',
  props: {
    text: [String, Number],
    type: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      active: false,
    };
  },
  computed: {
    className() {
      const types = this.type.slice(0);
      if (this.active) {
        types.push('active');
      }
      return types.map((type) => `vux-key-${type}`);
    },
  },
  methods: {
    onFocus() {
      this.active = true;
      this.$emit('press', this.text);
    },
    onBlur() {
      this.active = false;
    },
  },
};
</script>

<style lang="less">

.vux-key {
  display: inline-block;
  width: 33.33333333%;
  height: 54px;
  font-size: 24px;
  font-style: normal;
  line-height: 54px;
  text-align: center;
  vertical-align: middle;

  &::after {
    border-width: 1px 1px 0 0;
  }

  &-big {
    width: 100%;
    height: 108px;
    line-height: 108px;
  }

  &-delete {
    font-size: 0;
    background: url(data:image/png;base64,) no-repeat center;
    background-size: auto 37px;
  }

  &-hide {
    font-size: 0;
    background: url(data:image/png;base64,) no-repeat center;
    background-size: auto 37px;
  }

  &-active {
    background-color: #f2f3f5;
  }

  &-blue {
    background-color: var(--vux-primary-color);
    color: #fff;

    &.vux-key-active {
      background-color: var(--vux-primary-color);
    }
  }
}

.vux_hairline {
  position: relative;

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 200%;
    height: 200%;
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
    -webkit-transform-origin: 0 0;
    transform-origin: 0 0;
    pointer-events: none;
    box-sizing: border-box;
  }
}

</style>

